html, body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  /* overflow: hidden; */
}

#root {
  width: 100%;
  height: 100%;
}

/*  lazy placement and background for demo */

svg {
  width: 36vw;
  height: 36vh;
}

/*main-logo*/
.ha-logo path,
.ha-logo circle {
  fill: none;
  stroke: #99e1fd;
  stroke-width: 0.125px;
  stroke-linejoin: round;
}
.ha-logo path.house,
.ha-logo circle.house {
  stroke-linecap: round;
  stroke-width: .3px;
}

.loading.ha-logo circle {
  -webkit-animation: nodes 6s linear infinite;
          animation: nodes 6s linear infinite;
}

.loading.ha-logo .house {
  -webkit-animation: house 6s ease infinite;
          animation: house 6s ease infinite;
}

.loading.ha-logo .circut {
  -webkit-animation: circut 6s cubic-bezier(0.7, 0.1, 0.1, 0.9) infinite;
          animation: circut 6s cubic-bezier(0.7, 0.1, 0.1, 0.9) infinite;
}

@-webkit-keyframes house {
  0% {
    stroke-dashoffset: -50;
    stroke-dasharray: 50 50;
  }
  45% {
    stroke-dashoffset: -100;
  }
  50% {
    stroke: #99e1fd;
  }
  60% {
    stroke: white;
  }
  75% {
    stroke: #99e1fd;
  }
  80% {
    stroke-dashoffset: -100;
  }
  100% {
    stroke-dashoffset: -130;
    stroke-dasharray: 50 50;
  }
}

@keyframes house {
  0% {
    stroke-dashoffset: -50;
    stroke-dasharray: 50 50;
  }
  45% {
    stroke-dashoffset: -100;
  }
  50% {
    stroke: #99e1fd;
  }
  60% {
    stroke: white;
  }
  75% {
    stroke: #99e1fd;
  }
  80% {
    stroke-dashoffset: -100;
  }
  100% {
    stroke-dashoffset: -130;
    stroke-dasharray: 50 50;
  }
}
@-webkit-keyframes circut {
  0% {
    stroke-dasharray: 20 20;
    stroke-dashoffset: -20;
  }
  13% {
    stroke-dashoffset: -20;
  }
  50% {
    stroke-dashoffset: -40;
    stroke: #99e1fd;
  }
  60% {
    stroke: white;
  }
  70% {
    stroke: #99e1fd;
  }
  85% {
    stroke-dashoffset: -40;
  }
  100% {
    stroke-dashoffset: -60;
    stroke-dasharray: 20 20;
  }
}
@keyframes circut {
  0% {
    stroke-dasharray: 20 20;
    stroke-dashoffset: -20;
  }
  13% {
    stroke-dashoffset: -20;
  }
  50% {
    stroke-dashoffset: -40;
    stroke: #99e1fd;
  }
  60% {
    stroke: white;
  }
  70% {
    stroke: #99e1fd;
  }
  85% {
    stroke-dashoffset: -40;
  }
  100% {
    stroke-dashoffset: -60;
    stroke-dasharray: 20 20;
  }
}
@-webkit-keyframes nodes {
  0% {
    stroke-dasharray: 0 4;
  }
  25% {
    stroke-dasharray: 0 4;
  }
  35% {
    stroke-dasharray: 4 0;
    stroke: #99e1fd;
  }
  41% {
    fill: none;
  }
  42% {
    fill: #99e1fd;
  }
  55% {
    fill: none;
    stroke: #99e1fd;
  }
  65% {
    fill: white;
    stroke: white;
  }
  75% {
    stroke: #99e1fd;
    fill: none;
    stroke-dasharray: 4 0;
  }
  85% {
    fill: #99e1fd;
    stroke: #99e1fd;
  }
  93% {
    fill: none;
    stroke-dasharray: 4 0;
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -4;
    stroke-dasharray: 0 4;
  }
}
@keyframes nodes {
  0% {
    stroke-dasharray: 0 4;
  }
  25% {
    stroke-dasharray: 0 4;
  }
  35% {
    stroke-dasharray: 4 0;
    stroke: #99e1fd;
  }
  41% {
    fill: none;
  }
  42% {
    fill: #99e1fd;
  }
  55% {
    fill: none;
    stroke: #99e1fd;
  }
  65% {
    fill: white;
    stroke: white;
  }
  75% {
    stroke: #99e1fd;
    fill: none;
    stroke-dasharray: 4 0;
  }
  85% {
    fill: #99e1fd;
    stroke: #99e1fd;
  }
  93% {
    fill: none;
    stroke-dasharray: 4 0;
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -4;
    stroke-dasharray: 0 4;
  }
}
